<template>
  <div>
    <!--  -->
    <mt-header id="head" title="">
      <router-link to="/" slot="left">
        <mt-button icon="back"></mt-button>
      </router-link>
    </mt-header>
    <!-- logo -->
    <img src="../assets/logo.jpg" style="width:100%;height:;" alt />
    <!--  -->
    <label for="phone"></label>
    <img id="phone" src="../assets/shouji.png" />
    <mt-field placeholder="" v-model="uname"></mt-field>

    <label for="password"></label>
    <img id="password" src="../assets/yaoshi.png" />
    <mt-field placeholder="" type="password" v-model="upwd"></mt-field>

    <!--  -->
    <!-- <button id="d1" @click="login"></button> -->
    <mt-button id="d1" size="large" @click="login"> </mt-button>
  </div>
</template>

<script>
export default {
  data() {
    return {
      uname: "",
      upwd: ""
    };
  },
  methods: {
    login() {
      var ureg = /^[a-z0-9]{3,12}$/i;
      var u = this.uname;
      var p = this.upwd;
      if (ureg.test(u) == false) {
        this.$messagebox("", "");
        return;
      }
      if (ureg.test(p) == false) {
        this.$messagebox("", "");
        return;
      }
      var url = "login";
      var obj = { uname: u, upwd: p };
      this.axios.get(url, { params: obj }).then(res => {
        console.log(res.data.code);
        if (res.data.code < 0) {
          this.$messagebox("", "");
        } else {
          // console.log(res.data.uid);
          var uid = res.data.uid;
          sessionStorage.setItem("uid", uid);
          //  this.$router.push({path:"/"});
          this.$messagebox("", "").then(action => {
            this.$router.push({ path: "/" });
          });
        }
      });
    }
  }
}
</script>

<style>
html {
  width: 100%;
  margin: 0;
  padding: 0;
}
#head {
  width: 100%;
  height: 46px;
  /* padding: 0px; */
  font-size: 17px;
  font-family: ;
}
.mint-header {
  background: #fff;
  color: #282828;
  box-shadow: 0 0 1px #7d7f81;
}

#phone {
  float: left;
  width: 26px;
  height: 26px;
  margin-top: 10px;
  margin-left: 10px;
}
#password {
  float: left;
  width: 26px;
  height: 26px;
  margin-top: 10px;
  margin-left: 10px;
}
#d1 {
  color: #fff;
  background-color: #f1a43a;
  margin-top: 20px;
}
</style>



    


