<template>
  <div class="detail">
    <mt-header title>
      <router-link to="/fangjianCell" slot="left">
        <mt-button icon="back"></mt-button>
      </router-link>
      <mt-button slot="right" @click="addCart">
        <img style="width:20px;" src="../../assets/detail/shoucang.png" v-show="downIcon" alt />
        <img style="width:21px;" src="../../assets/detail/shoucang2.png" v-show="!downIcon" alt />
      </mt-button>
    </mt-header>
    <van-swipe @change="onChange" class="detail-lb">
      <van-swipe-item v-for="(item,i) of tImg" :key="i">
        <img style="width:100%;" :src="url+item.imgurl" alt />
      </van-swipe-item>
      <div class="custom-indicator" slot="indicator">
        <div>{{ current + 1 }}/4</div>
      </div>
    </van-swipe>
    <div class="detail-xiangqing">
      <h2>{{details.title}}</h2>
      <p class="dt-p2">
         {{details.price}}
        <span>/</span>
      </p>
      <van-divider />
      <ul class="dt-ul qingchu">
        <li>
           :
          <span>{{details.leixing}}</span>
        </li>
        <li>
           :
          <span>{{details.huxing}}</span>
        </li>
        <li>
           :
          <span>{{details.louceng}}/30</span>
        </li>
        <li>
           :
          <span>{{details.mianji}}</span>
        </li>
        <li>
           :
          <span>{{details.chaoxiang}}</span>
        </li>
      </ul>
      <p class="dt-p3"></p>
      <p class="dt-p3 p4">
        
        
        
      </p>
      <img style="width:100%;" src="../../assets/timg.jpg" alt />
      <p class="dt-p3"></p>
      <ul class="dt-fuwu">
        <li>
          <img src="../../assets/detail/wifi.png" alt />
          <p>wifi</p>
        </li>
        <li>
          <img src="../../assets/detail/kongtiao.png" alt />
          <p></p>
        </li>
        <li>
          <img src="../../assets/detail/dianshi.png" alt />
          <p></p>
        </li>
        <li>
          <img src="../../assets/detail/xiyiji.png" alt />
          <p></p>
        </li>
        <li>
          <img src="../../assets/detail/bingxiang.png" alt />
          <p></p>
        </li>
        <li>
          <img src="../../assets/detail/reshui.png" alt />
          <p></p>
        </li>
        <li>
          <img src="../../assets/detail/yigui.png" alt />
          <p></p>
        </li>
        <li>
          <img src="../../assets/detail/tianranqi.png" alt />
          <p></p>
        </li>
        <li>
          <img src="../../assets/detail/chuang.png" alt />
          <p></p>
        </li>
        <li>
          <img src="../../assets/detail/baojie.png" alt />
          <p></p>
        </li>
      </ul>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      uid: "",
      hid: "",
      tImg: [],
      details: [],
      current: 0,
      downIcon:true,
      url:"http://118.190.162.12:4000/"
    };
  },
  methods: {
    onChange(index) {
      this.current = index;
    },
    addCart(event) {
      this.downIcon=!this.downIcon;
      //4.obj
      this.axios
        .get("addcart", {
          params: {
            hid: this.hid,
            count: 1,
            img: this.tImg[0].imgurl,
            title: this.details.title,
            address: this.details.address,
            price: this.details.price,
            uid: this.uid,
            state:1
          }
        })
        .then(res => {
          //5.
          var code = res.data.code;
          if (code == -1) {
            this.$messagebox("", "").then(res => {
              this.$router.push("/login");
            });
          }else if(code==0){
            this.axios.get("del",{params:{
              hid:this.hid
            }}).then(res=>{
              if(res.data.code==1){
                this.$toast("");
              }
            })
          }else {
            this.$toast("");
          }
        });
    },
    load() {
        var uid = sessionStorage.getItem("uid");
        this.uid = uid;
        console.log(uid);
        var hid = this.$route.query.hid;
        this.hid = hid;
        this.axios.get("detail", { params: { hid } }).then(res => {
        console.log(res.data.data);
        this.tImg = res.data.data.hImg;
        this.details = res.data.data.detail[0];
        // console.log(this.details[0]);
      });
    }
  },
  created() {
    this.load();
  }
};
</script>
<style>
.qingchu:after {
  display: block;
  content: "";
  clear: both;
}
/*  */
.mint-header {
  background-color: #ed8117;
  font-size: 15px;
  height: 45px;
}
/*  */
.detail-lb {
  position: relative;
}
.custom-indicator {
  position: absolute;
  right: 20px;
  bottom: 15px;
}
.custom-indicator div {
  width: 50px;
  height: 30px;
  background: black;
  opacity: 0.6;
  color: #fff;
  font-size: 16px;
  line-height: 30px;
  border-radius: 50%;
}
/*  */
.detail-xiangqing h2 {
  font-size: 20px;
  text-align: left;
  padding-left: 20px;
}
.detail-xiangqing .dt-p2 {
  font-size: 18px;
  text-align: left;
  padding-left: 20px;
  color: #f00;
}
.dt-p3 {
  text-align: left;
  padding-left: 10px;
  color: #666;
}
.p4 {
  font-size: 14px;
  line-height: 25px;
}
.detail-xiangqing p span {
  font-size: 12px;
}
.dt-ul {
  text-align: left;
  color: #999;
  font-size: 14px;
  padding-left: 20px;
}
.dt-ul li {
  width: 50%;
  float: left;
  padding: 10px 0;
}
.dt-ul li span {
  color: #333;
  font-size: 16px;
}
/*  */
.dt-fuwu li {
  float: left;
  width: 20%;
}
.dt-fuwu p {
  color: #ed8117;
  font-size: 12px;
}
</style>



